.grid-container {
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-columns: 50fr 100vw 50fr;
  grid-template-rows: 10rem 5rem auto 100fr 2rem;
  overflow: hidden;
  grid-template-areas: ". header ." "nav nav nav" ". player ." "main main main" ". footer .";
}

header.site-header {
  grid-area: header;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-start;
  border-bottom: 1px solid black;
}

header.site-header .about-container {
  padding: 1.5rem;
}

.player {
  grid-area: player;
}

nav {
  grid-area: nav;
}

div.main {
  grid-area: main;
  width: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: flex-start;
  justify-content: flex-start;
  overflow-x: scroll;
  padding: 3rem;
}

div.main .entry {
  width: 20rem;
  margin-right: 2rem;
  flex-grow: 0;
  flex-shrink: 0;
}

div.main .entry.selected {
  align-self: stretch;
  width: 90vw;
  max-width: 60rem;
}

footer {
  grid-area: footer;
}

.logo {
  max-width: 8rem;
}
